  .five {
    text-align: left;
      margin-left: 100px;
      margin-right: 100px;
      margin-top: -1030px;
      color: rgb(255, 255, 255);
      font-size: 20px;
      background-color: rgb(31, 31, 31);
      line-height: 80px;
      border-radius: 0px;
      padding: 40px 40px;
       font-weight: 400;
       line-height: 1.5;
      font-family: 'Raleway', sans-serif;
  }

  .prev, .next {
    cursor: pointer;
    position: sticky;
    top: 50%;
    width: auto;
    margin-top: 80px;
    padding: 16px;
    color: rgb(179, 173, 173);
    font-weight: bold;
    font-size: 30px;
    transition: 0.6s ease;
    border-radius: 0 3px 3px 0;
    user-select: none;
    background-color: black;
   }
  
   /* Position the "next button" to the right */
   .next {
    right: 0px;
    border-radius: 3px 0 0 3px;
    margin-left: 1470px;
   }
  
  
   .prev {
    left: 0;
    border-radius: 3px 0 0 3px;
  
   }
  
   /* On hover, add a black background color with a little bit see-through */
   .prev:hover, .next:hover {
    background-color: rgba(157, 149, 149, 0.8);
    color: black;
   }
  
  
    
    @media (max-width: 800px) {
      
       .name5 {
        color: rgb(242, 214, 214);
        font-size: 60px;
        font-family: Lucida handwriting;
        font-weight: bold;
        line-height: px;
        margin-left: px;
        position: left;  
        padding: 10px 10px;
        text-align: center;
      }
  
      #check:checked ~ ul {
        left: 0;
      }

      @media only screen and (max-width:800px) {
        /* For tablets: */
        .main {
          width: 80%;
          padding: 0;
        }
        .right {
          width: 100%;
        }
    }
  }
      